    </header>
    <!-- 主体 -->
    <div class="main" v-for="(item,index) in list" :key="index">
      <div class="h1">
        <h1>{{item.h1}}</h1>
      </div>
      <div class="p1">
        <p>{{item.p1}}</p>
      </div>
      <div class="p1">
        <p>
          {{item.p2}}
          <br />
          {{item.p3}}
        </p>
      </div>
      <footer class="fl-ex al-it">
        <p style="margin-left:38%;">阅读</p>
        <p style="margin-left:2%;">|</p>
        <i class="el-icon-view"></i>
        <span>{{item.num}}</span>
      </footer>
    </div>
  </div>
  <!-- 吃不起，真的吃不起，吃不起，吃不起，公司里着啊，头疼俩 -->
