export default {
  data() {
    return {
      list: [
        {
          h1: "释放职工网民正能量 构建网上网下同心圆 省总工会深入开展“网聚职工正能量.争做中国好网民”主题活动",
          p1:
            "日前，青海省总工会突出工会工作特色和优势，以内容建设、阵地建设、机制建设为抓手，从基础、载体、阵地、机制四方面下功夫，持续深入开展 “网聚职工正能量·争做中国好网民” 主题活动，进一步弘扬工会网上正能量，提高工会网上舆论传播力、引导力、影响力。青海省总以广泛传播和践行中国好网民理念、引导广大职工和工会干部学好用好网络技术、提升网络文明素养为目标，通过打造“三个一批”。即：培育一批职工好网民，开展一批职工好网民网络文化活动，打造一批职工好网民品牌项目，在全省范围内开展百场系列活动，充分利用互联网的辐射性和交互性，发挥“网络倍增效应”，力争参与和覆盖超过万名职工群众，形成强大正面效应。",
          p2:
            "青海省总充分发挥工会组织优势和职工群众资源，在全省工会系统开展网民素养教育和 “好网民正能量” 示范性网络文化作品征集活动。各级工会组织深入园区、企业、车间等基层一线，征集广大职工原创的正能量作品和一线劳动模范、技术能手立足岗位、守正创新的图文及音视频资料。",
          p3:
            "同时，在省总工会门户网站、青海工会微信公众号等平台开辟专栏，面向社会广泛征集作品并择优进行展播。各级工会同步开展丰富多彩的线下活动，宣传网络安全知识，开展网民素养教育和个人信息保护教育，形成了全省各级工会协同联动、齐抓共管的良好工作局面",
          num: "2230"
        }
      ]
